<template>
  <v-navigation-drawer v-model="isOpen" fixed right width="450px">
    <v-card :loading="loadingData" style="height: 100%">
      <v-toolbar flat>
        <v-toolbar-title>{{ pluginName }}</v-toolbar-title>
        <v-spacer />
        <v-icon class="mr-6" color="primary" @click="postConfig" :disabled="!valid">mdi-content-save</v-icon>
        <v-icon @click="onClose">mdi-close</v-icon>
      </v-toolbar>
      <v-divider></v-divider>
      <v-container class="pa-4 mt-4">
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-textarea
            name="input-7-4"
            label="Enter config"
            v-model="config"
            outlined
            :rules="[() => isCorrectKey(config) || 'Only change value! Please undo.', rules.required]"
          />
        </v-form>
      </v-container>
    </v-card>
  </v-navigation-drawer>
</template>

<script>
import * as pluginService from '../../services/pluginService';

export default {
  name: 'config-form',
  props: ['onClose', 'isOpen', 'pluginName'],
  data: () => ({
    rules: {
      required: value => !!value || 'Required.',
    },
    valid: false,
    loadingData: false,
    config: "",
  }),
  computed: {},
  watch: {
    isOpen(val){
      if(val) this.getConfigs();
    },
  },
  methods: {
    getConfigs() {
      this.loadingData = true;
      pluginService.fetchPluginsConfigByName(this.pluginName)
        .then(response => {
          this.config = response;
        }).catch(()=> {
          this.config = ""
        })
        .finally(() => {
          this.loadingData = false;
        });
    },
    postConfig() {
      const postData = {data: this.config};
      pluginService.patchPluginsConfigByName(this.pluginName, postData)
      .then(data => {
        this.$toasted.show(`Config is updated successfully!`);
      })
      .catch(ex=>{
        this.$toasted.show(`Check your YAML config!`, { type: 'error' });
      })
      .finally(() => {
        this.onClose();
      });
    },
    isCorrectKey(config){
      return true // TODO: YAML validator
    }
  },
};
</script>


